import { forwardRef } from "react"
import { Backdrop, Fade } from "@rp/react/web/mui/core"
import { GenericModalStyled, PaperStyled } from "./GenericModal.styles"

type Props = {
  children: React.ReactNode
  isOpen: boolean
  handleClose?: () => void
  ariaLabel?: string
}

const GenericModalWrapper = forwardRef<HTMLDivElement | null, Props>(
  ({ children, isOpen, handleClose, ariaLabel }, ref) => (
    <GenericModalStyled
      open={isOpen}
      onClose={handleClose}
      closeAfterTransition
      disableRestoreFocus
      BackdropComponent={Backdrop}
      BackdropProps={{
        timeout: 500,
      }}
      aria-label={ariaLabel}
      ref={ref}
    >
      <Fade in={isOpen}>
        <PaperStyled role="alertdialog">{children}</PaperStyled>
      </Fade>
    </GenericModalStyled>
  ),
)

export { GenericModalWrapper }
